// Variables
$orokin-gold: #DAA520;
$orokin-dark-gold: #B8860B;
$orokin-light-gold: #FFD700;
$orokin-background: #1A1A1A;
$orokin-secondary-bg: #2A2A2A;
$orokin-text: #EDEDED;
$orokin-subtext: #B0B0B0;
$orokin-card: #333333;
$orokin-blue-accent: #1E90FF;
$orokin-red-accent: #B22222;
$orokin-border: #4A4A4A;

@mixin glass{
    background: rgba( 0, 0, 0, 0.7 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 6px );
    -webkit-backdrop-filter: blur( 4px );
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

/* Animations */
@keyframes pulser {
	0% {
        width: 52px;	height: 52px;
    }
    10% {
        width: 52px;	height: 52px;
    }
    20% {
        width: 72px;	height: 72px;
    }
    35% {
        width: 72px;	height: 72px;
    }
    45% {
            width: 52px;	height: 52px;
    }
    60% {
        width: 52px;	height: 52px;
    }
    75% {
        width: 72px;	height: 72px;
    }
    85% {
            width: 72px;	height: 72px;
    }
    100% {
        width: 52px;	height: 52px;
    }
}
@keyframes spinner {
	0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(0deg);
    }
    20% {
        transform: rotate(90deg);
    }
    35% {
        transform: rotate(90deg);
    }
    45% {
        transform: rotate(180deg);
    }
    60% {
        transform: rotate(180deg);
    }
    75% {
        transform: rotate(270deg);
    }
    85% {
        transform: rotate(270deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@keyframes fadeOut {
    0% {
        background-color: $orokin-background;
    }
    100% {
        background-color: rgb(0,0,0,0);
    }
}

/* Scrollbar */
::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    background: $orokin-secondary-bg;
}
::-webkit-scrollbar-thumb {
    background: #555;
}
::-webkit-scrollbar-thumb:hover {
    background: #7c7c7c;
}

/* General styles */
* {
    transition: all 0.2s ease-in-out;
}
:root{
    // max-width: 2440px;
    display: flex;
    justify-content: center;
}
html{
    display: flex;
    justify-content: center;
    background: url("./assets/Empyrean.jpg") no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}
body {
    font-family: 'Arial', sans-serif;
    // background-color: $orokin-background;

    color: $orokin-text;
    margin: 1% 10%;
}
a {
    color: $orokin-blue-accent;
    text-decoration: none;
    &:hover {
        color: $orokin-light-gold;
    }
}
h1, h2, h3, p{
    // user-select: none;
    cursor: default;
}
div {
    & > h3 {
        color: $orokin-subtext;
        margin-top: 0;
        padding-top: 20px;
    }
    & > h2 {
        color: $orokin-gold;
        margin-top: 0;
        padding-top: 20px;
    }
    & > p {
        color: $orokin-subtext;
    }
}
hr{
    border: 1px solid $orokin-subtext;
    margin: 25px 0;
    filter: drop-shadow(0px 0px 5px $orokin-gold);
}

/* Elements */
.modal{
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    &_content {
        @include glass;
        border: 1px solid $orokin-border;
        padding: 1em;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        max-width: 80%;
        &-btn{
            background-color: $orokin-gold;
            border: none;
            padding: 0.5em 1em;
            height: 30px;
            cursor: pointer;
            transition: all 0.2s ease-in-out;
            &:hover {
                background-color: $orokin-light-gold;
            }
        }
    }
}
.alert {
    background-color: $orokin-red-accent;
    color: $orokin-background;
    padding: 1em;
    border-radius: 4px;
}
.accordion{
    border-radius: 6px;
    border: 1px solid transparent;
    &:hover{
        background: rgba( 0, 0, 0, 0.8 );
        border: 1px solid $orokin-blue-accent;
    }
    &-content{
        padding: 0 10px 20px 10px;
        ul{
            padding-left: 20px;
        }
    }
    & h3 {
        display: flex;
        align-items: center;
        cursor: pointer;
        margin: 0;
        padding: 20px 10px 20px;
    }
    .arrow {
        margin-left: 10px;
        transition: transform 0.3s ease;
    }
    .arrow.open {
        transform: rotate(180deg);
    }
}

.App{
    .planetStates{
        display: flex;
        flex: 0 1 calc(33.333% - 10px);
        margin: 5px;
        justify-content: space-between;

        &-content{
            @include glass;
            padding-left: 10px;
            border: #2A2A2A 1px solid;
            border-radius: 4px;
            transition: all 0.2s ease-in-out;
            border: 1px solid transparent;
            min-height: 150px;
            padding: 10px;
            width: 225px;

        }
    }

    .voidTrader{
        @include glass;
        margin-top: 25px;
        padding: 10px 25px;
        border-radius: 4px;
        border: 1px solid $orokin-border;
    }

    &_Search{
        width: 100%;
        display: flex;
        justify-content: space-between;

        .WarframeDataDisplay{
            max-height: 500px;
            overflow-y: scroll;
            overflow-x: hidden;
            border-left: 1px solid $orokin-border;
            border-right: 1px solid $orokin-border;
            border-bottom: 1px solid $orokin-border;
            border-bottom-left-radius: 4px;
            border-bottom-right-radius: 4px;
        }

        .noResultsText{
            color: #EDEDED;
            text-align: center;
        }

        .Search{
            width: 24%;

            form {
                @include glass;
                border: 1px solid $orokin-border;
                padding: 0.5em 1em;
                height: 100px;
                display: flex;
                border-radius: 4px;
                flex-direction: column;
                align-items: center;
                justify-content: space-evenly;

                input[type="text"] {
                    width: 96%;
                    padding: 0.5em;
                    border: 1px solid $orokin-dark-gold;
                    background-color: $orokin-secondary-bg;
                    color: $orokin-text;

                    &::placeholder {
                        color: $orokin-subtext;
                    }

                    &:hover{
                        border: 1px solid $orokin-light-gold;
                        background-color: lighten($color: $orokin-secondary-bg, $amount: 2.5);
                    }
                }

                button {
                    background-color: $orokin-gold;
                    border: none;
                    padding: 0.5em 1em;
                    height: 30px;
                    width: 100%;
                    cursor: pointer;
                    transition: all 0.2s ease-in-out;

                    &:hover {
                        background-color: $orokin-light-gold;
                    }
                }

                .deleteResults{
                    transition: all 0.2s ease-in-out;
                    background-color: #B22222;
                    padding: 0.5em 1em;
                    height: 30px;
                    width: 100%;

                    &:hover{
                        background-color: #ff4343;
                    }
                }
            }
        }

        .warframes, .mods, .weapons, .fissureDrops {
            @include glass;
            position: relative;
            padding: 3%;
        }

        .fissureDrops {
            background-color: #2A2A2A;

            &-fissure {
                border-radius: 10px;
                padding: 10px 25px;
                transition: all 0.2s ease-in-out;
                border: 1px solid transparent;
            }
        }
    }

    &_Activities{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;

        .sortie, .syndicatMissions, .nightwaves, .fissures {
            @include glass;
            padding: 10px 25px;
            border-radius: 4px;
            border: 1px solid $orokin-border;
            width: 20%;
            max-width: 370px;
            min-width: 300px;
            max-height: 600px;
            overflow-y: scroll;
            overflow-x: hidden;
        }

        .fissures {
            &-content {
                padding: 10px 25px;
                border-radius: 4px;
                border: 1px solid transparent;
            }
        }
    }

    // Media Queries for Responsiveness
    @media (max-width: 2070px) {
        // Adjustments for large screens (larger than tablets, smaller than desktops)
        max-width: 600px !important;
        .planetStates {
            flex-wrap: wrap;
            justify-content: center;
            &-content{
                width: 97.5%;
                min-height: 120px;
                margin-top: 20px;
            }
            .WarframeDataDisplay{
                width: 100%;
                margin-top: 20px;
            }
        }
        &_Search {
            flex-direction: column;

            .Search {
                width: 100%;
            }
        }
        &_Activities {
            .sortie, .syndicatMissions, .nightwaves, .fissures {
                width: 100%;
                max-width: 100%;
                max-height: fit-content;
                margin-bottom: 20px;
            }
        }
    }

    @media (max-width: 991px) {
        // Adjustments for tablets
        max-width: 600px !important;
        .planetStates {
            flex-wrap: wrap;
            justify-content: center;
            &-content{
                width: 97.5%;
                min-height: 120px;
                margin-top: 20px;
            }
            .WarframeDataDisplay{
                width: 100%;
                margin-top: 20px;
            }
        }
        &_Search {
            flex-direction: column;

            .Search {
                width: 100%;
            }
        }
        &_Activities {
            .sortie, .syndicatMissions, .nightwaves, .fissures {
                width: 100%;
                max-width: 100%;
                max-height: fit-content;
                margin-bottom: 20px;
            }
        }
    }


}


//loading screen
#loader{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    @include glass;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: rgb(255,255,255);
    animation: fadeOut 3000ms ease-in-out forwards;
}
#loader_wrapper{
    z-index: 9999;
	background: rgb(255,255,255);
	box-sizing: content-box;
	box-shadow: 0 0 0 96px $orokin-background inset;
	width: 200px;
	height: 200px;
	overflow: hidden;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%) rotate(45deg);
}
#loader_sides, #loader_center{
	border: 6px solid rgb(255,255,255);
	display: inline-block;
	position: absolute;
}
#loader_center{
	top: 41%;
	left: 41%;
	transform: translate(-50%,-50%);
	width: 24px;
	height: 24px;
	animation-name: spinner;
    animation-duration: 3s;
    animation-iteration-count: infinite;
	animation-timing-function: linear;
}
#loader_sides{
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 52px;
	height: 52px;
	animation-name: pulser;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
	animation-timing-function: linear;
}
#loader_sides::before, #loader_sides::after{
    background-color: $orokin-background;
	content: ' ';
	width: 80px;
	height: 200px;
	display: inline-block;
    position: absolute;
	transform: rotate(45deg);
}
#loader_sides::before{
	bottom: -110px;
	left: 28px;
}
#loader_sides::after{
	top: -110px;
	right: 28px;
}